nav::before {
    content: '';
    position: absolute;
    background: rgba(0, 0, 0, 0.1);
    z-index: -1;
    width: 100%;
    height: 100%;
}

#home_main {
    background-image: url(./image045.jpg);
    background-position: center;
    background-repeat: no-repeat;
    background-size:cover;
    z-index: -1;
    position: absolute;
}

#home_in {  
    backdrop-filter: blur(18px);
    -webkit-backdrop-filter:blur(18px);  
    background: linear-gradient(135deg,rgba(100, 100, 100, 0.3),rgba(22, 21, 21, 0.4));
}
#home_button:hover img {
    width: 5%;
    overflow: hidden;
    margin-left: 2.5%;
}
#home_button img {
    transition: 1s;
}
body {
    font-family: 'Nunito Sans', sans-serif;
}
* {
    overflow-x: hidden;
    scroll-behavior: smooth;;
}